@import '../theme.scss';

.Login {
  background-color: $themeRed;
  border-radius: 10px;
  height: 40px;
  width: 100%;
  margin-top: 80px;
}

.MovieTab{
  border: none;
  background-color: $themeLightGray;
  height: 30px;
  width: auto;
  max-width: max-content;
  column-span: 100%;
  border-radius: 10px;
}


.CreateList {
  background-color: $themeRed;
  border-radius: 10px;
  height: 40px;
  width: 80px;
  transition: 0.3s;
}

.CreateList:hover {
  background-color: $themeRed;
  border-radius: 10px;
  height: 45px;
  width: 85px;
}

